.button {
    border :none;
    color:white;
    padding:15px 32px;
    text-align:'center';
    text-decoration: none;
    display: inline-block;
    font-size:16px;
    border-radius: 4px;
    cursor:pointer;
}

.primary {background-color: blue;}
.secondary {background-color:orange}
.success {background-color:green;}
.danger {background-color: red;}